  border-color: rgba(0, 0, 0, 0.03);\n        }\n\n        &:hover {\n          > td {\n            background: @table-selected-row-hover-bg;\n          }\n        }\n      }\n    }\n  }\n\n  // =========================== Summary ============================\n  tfoot {\n    > tr {\n      > th,\n      > td {\n        border-bottom: @border-width-base @border-style-base @table-border-color;\n      }\n    }\n  }\n\n  // ========================== Pagination ==========================\n  &-pagination.@{ant-prefix}-pagination {\n    margin: 16px 0;\n  }\n\n  &-pagination {\n    display: flex;\n\n    &-left {\n      justify-content: flex-start;\n    }\n\n    &-center {\n      justify-content: center;\n    }\n\n    &-right {\n      justify-content: flex-end;\n    }\n  }\n\n  // ================================================================\n  // =                           Function                           =\n  // ================================================================\n\n  // ============================ Sorter ============================\n  &-thead th.@{table-prefix-cls}-column-has-sorters {\n    padding: 0;\n    cursor: pointer;\n    transition: all 0.3s;\n\n    &:hover {\n      background: @table-header-sort-active-bg;\n\n      .@{table-prefix-cls}-filter-trigger-container {\n        background: @table-header-sort-active-filter-bg;\n      }\n    }\n  }\n\n  &-thead th.@{table-prefix-cls}-column-sort {\n    background: @table-header-sort-bg;\n  }\n  td&-column-sort {\n    background: @table-body-sort-bg;\n  }\n\n  &-column-sorters-with-tooltip {\n    display: inline-block;\n    width: 100%;\n  }\n\n  &-column-sorters {\n    display: inline-flex;\n    align-items: center;\n    padding: @table-padding-vertical @table-padding-horizontal;\n  }\n\n  &-column-sorter {\n    margin-top: 0.15em;\n    margin-bottom: -0.15em;\n    margin-left: @padding-xs;\n    color: @table-header-icon-color;\n\n    &-full {\n      margin-top: -0.2em;\n      margin-bottom: 0;\n    }\n\n    &-inner {\n      display: inline-flex;\n      flex-direction: column;\n      align-items: center;\n    }\n\n    &-up,\n    &-down {\n      font-size: 11px;\n\n      &.active {\n        color: @primary-color;\n      }\n    }\n\n    &-up + &-down {\n      margin-top: -0.3em;\n    }\n  }\n\n  // ============================ Filter ============================\n  &-filter-column {\n    display: flex;\n    align-items: center;\n    margin: -@table-padding-vertical -@table-padding-horizontal;\n  }\n\n  &-filter-column-title {\n    flex: auto;\n    padding: @table-padding-vertical 2.3em @table-padding-vertical @table-padding-horizontal;\n  }\n\n  // Remove padding when sorter also provided\n  &-thead tr th.@{table-prefix-cls}-column-has-sorters {\n    .@{table-prefix-cls}-filter-column {\n      margin: 0;\n    }\n\n    .@{table-prefix-cls}-filter-column-title {\n      padding: 0 2.3em 0 0;\n    }\n  }\n\n  &-filter-trigger-container {\n    position: absolute;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    display: flex;\n    flex: none;\n    align-items: stretch;\n    align-self: stretch;\n    cursor: pointer;\n    transition: background-color 0.3s;\n\n    &-open,\n    &:hover,\n    .@{table-prefix-cls}-thead th.@{table-prefix-cls}-column-has-sorters:hover &:hover {\n      background: @table-header-filter-active-bg;\n    }\n  }\n\n  &-filter-trigger {\n    display: block;\n    width: 2.3em;\n    color: @table-header-icon-color;\n    font-size: @font-size-sm;\n    transition: color 0.3s;\n\n    .@{iconfont-css-prefix} {\n      position: absolute;\n      top: 50%;\n      left: 50%;\n      transform: translate(-50%, -50%);\n    }\n\n    .@{table-prefix-cls}-filter-trigger-container-open &,\n    &:hover {\n      color: @text-color-secondary;\n    }\n\n    &.active {\n      color: @primary-color;\n    }\n  }\n\n  // Dropdown\n  &-filter-dropdown {\n    .reset-component();\n\n    // Reset menu\n    .@{dropdown-prefix-cls}-menu {\n      // https://github.com/ant-design/ant-design/issues/4916\n      // https://github.com/ant-design/ant-design/issues/19542\n      max-height: 264px;\n      overflow-x: hidden;\n      border: 0;\n      box-shadow: none;\n    }\n\n    min-width: 120px;\n    background-color: @table-filter-dropdown-bg;\n\n    border-radius: @border-radius-base;\n    box-shadow: @box-shadow-base;\n\n    &-submenu > ul {\n      max-height: calc(100vh - 130px);\n      overflow-x: hidden;\n      overflow-y: auto;\n    }\n\n    // Checkbox\n    &,\n    &-submenu {\n      .@{ant-prefix}-checkbox-wrapper + span {\n        padding-left: 8px;\n      }\n    }\n\n    // Operation\n    &-btns {\n      display: flex;\n      justify-content: space-between;\n      padding: 7px 8px 7px 3px;\n      overflow: hidden;\n      background-color: @table-filter-btns-bg;\n      border-top: @border-width-base @border-style-base @table-border-color;\n    }\n  }\n\n  // ========================== Selections ==========================\n  .@{table-prefix-cls}-selection-col {\n    width: @table-selection-column-width;\n  }\n\n  table tr th&-selection-column,\n  table tr td&-selection-column {\n    padding-right: @padding-xs;\n    padding-left: @padding-xs;\n    text-align: center;\n\n    .@{ant-prefix}-radio-wrapper {\n      margin-right: 0;\n    }\n  }\n\n  &-selection {\n    position: relative;\n    display: inline-flex;\n    flex-direction: column;\n\n    &-extra {\n      position: absolute;\n      top: 0;\n      z-index: 1;\n      cursor: pointer;\n      transition: all 0.3s;\n      margin-inline-start: 100%;\n      padding-inline-start: @padding-xss;\n\n      .@{iconfont-css-prefix} {\n        color: @table-header-icon-color;\n        font-size: 10px;\n\n        &:hover {\n          color: @table-header-icon-color-hover;\n        }\n      }\n    }\n  }\n\n  // ========================== Expandable ==========================\n  &-expand-icon-col {\n    width: 48px;\n  }\n\n  &-row-expand-icon-cell {\n    text-align: center;\n  }\n\n  &-row-indent {\n    float: left;\n    height: 1px;\n  }\n\n  &-row-expand-icon {\n    .operation-unit();\n    position: relative;\n    display: inline-flex;\n    float: left;\n    box-sizing: border-box;\n    width: @expand-icon-size;\n    height: @expand-icon-size;\n    padding: 0;\n    color: inherit;\n    line-height: ceil(((@font-size-sm * 1.4 - @border-width-base * 3) / 2)) * 2 + @border-width-base *\n      3;\n    background: @table-expand-icon-bg;\n    border: @border-width-base @border-style-base @table-border-color;\n    border-radius: @border-radius-base;\n    outline: none;\n    transform: scale((unit(@checkbox-size) / unit(@expand-icon-size)));\n    transform-origin: bottom;\n    transition: all 0.3s;\n    user-select: none;\n    @expand-icon-size: ceil(((@font-size-sm * 1.4 - @border-width-base * 3) / 2)) * 2 +\n      @border-width-base * 3;\n\n    &:focus,\n    &:hover,\n    &:active {\n      border-color: currentColor;\n    }\n\n    &::before,\n    &::after {\n      position: absolute;\n      background: currentColor;\n      transition: transform 0.3s ease-out;\n      content: '';\n    }\n\n    &::before {\n      top: ceil(((@font-size-sm * 1.4 - @border-width-base * 3) / 2));\n      right: 3px;\n      left: 3px;\n      height: @border-width-base;\n    }\n\n    &::after {\n      top: 3px;\n      bottom: 3px;\n      left: ceil(((@font-size-sm * 1.4 - @border-width-base * 3) / 2));\n      width: @border-width-base;\n      transform: rotate(90deg);\n    }\n\n    // Motion effect\n    &-collapsed::before {\n      transform: rotate(-180deg);\n    }\n    &-collapsed::after {\n      transform: rotate(0deg);\n    }\n\n    &-spaced {\n      &::before,\n      &::after {\n        display: none;\n        content: none;\n      }\n      background: transparent;\n      border: 0;\n      visibility: hidden;\n    }\n\n    .@{table-prefix-cls}-row-indent + & {\n      margin-top: ((@font-size-base * @line-height-base - @border-width-base * 3) / 2) -\n        ceil(((@font-size-sm * 1.4 - @border-width-base * 3) / 2));\n      margin-right: @padding-xs;\n    }\n  }\n\n  tr&-expanded-row {\n    &,\n    &:hover {\n      > td {\n        background: @table-expanded-row-bg;\n      }\n    }\n\n    // https://github.com/ant-design/ant-design/issues/25573\n    .@{descriptions-prefix-cls}-view {\n      display: flex;\n\n      table {\n        flex: auto;\n        width: auto;\n      }\n    }\n  }\n\n  // With fixed\n  .@{table-prefix-cls}-expanded-row-fixed {\n    position: relative;\n    margin: -@table-padding-vertical -@table-padding-horizontal;\n    padding: @table-padding-vertical @table-padding-horizontal;\n  }\n\n  // ========================= Placeholder ==========================\n  &-tbody > tr&-placeholder {\n    text-align: center;\n    .@{table-prefix-cls}-empty & {\n      color: @disabled-color;\n    }\n    &:hover {\n      > td {\n        background: @component-background;\n      }\n    }\n  }\n\n  // ============================ Fixed =============================\n  &-cell-fix-left,\n  &-cell-fix-right {\n    position: -webkit-sticky !important;\n    position: sticky !important;\n    z-index: @zindex-table-fixed;\n    background: @table-bg;\n  }\n\n  &-cell-fix-left-first::after,\n  &-cell-fix-left-last::after {\n    position: absolute;\n    top: 0;\n    right: 0;\n    bottom: -1px;\n    width: 30px;\n    transform: translateX(100%);\n    transition: box-shadow 0.3s;\n    content: '';\n    pointer-events: none;\n  }\n  &-cell-fix-right-first::after,\n  &-cell-fix-right-last::after {\n    position: absolute;\n    top: 0;\n    bottom: -1px;\n    left: 0;\n    width: 30px;\n    transform: translateX(-100%);\n    transition: box-shadow 0.3s;\n    content: '';\n    pointer-events: none;\n  }\n\n  .@{table-prefix-cls}-container {\n    &::before,\n    &::after {\n      position: absolute;\n      top: 0;\n      bottom: 0;\n      z-index: 1;\n      width: 30px;\n      transition: box-shadow 0.3s;\n      content: '';\n      pointer-events: none;\n    }\n\n    &::before {\n      left: 0;\n    }\n    &::after {\n      right: 0;\n    }\n  }\n\n  &-ping-left {\n    &:not(.@{table-prefix-cls}-has-fix-left) .@{table-prefix-cls}-container {\n      position: relative;\n\n      &::before {\n        box-shadow: inset 10px 0 8px -8px darken(@shadow-color, 5%);\n      }\n    }\n\n    .@{table-prefix-cls}-cell-fix-left-first::after,\n    .@{table-prefix-cls}-cell-fix-left-last::after {\n      box-shadow: inset 10px 0 8px -8px darken(@shadow-color, 5%);\n    }\n  }\n\n  &-ping-right {\n    &:not(.@{table-prefix-cls}-has-fix-right) .@{table-prefix-cls}-container {\n      position: relative;\n\n      &::after {\n        box-shadow: inset -10px 0 8px -8px darken(@shadow-color, 5%);\n      }\n    }\n\n    .@{table-prefix-cls}-cell-fix-right-first::after,\n    .@{table-prefix-cls}-cell-fix-right-last::after {\n      box-shadow: inset -10px 0 8px -8px darken(@shadow-color, 5%);\n    }\n  }\n  &-sticky {\n    &-header {\n      position: sticky;\n      z-index: @table-sticky-zindex;\n    }\n    &-scroll {\n      position: sticky;\n      bottom: 0;\n      z-index: @table-sticky-zindex;\n      display: flex;\n      align-items: center;\n      background: lighten(@table-border-color, 80%);\n      border-top: 1px solid @table-border-color;\n      opacity: 0.6;\n      &:hover {\n        transform-origin: center bottom;\n      }\n      &-bar {\n        height: 8px;\n        background-color: @table-sticky-scroll-bar-bg;\n        border-radius: @table-sticky-scroll-bar-radius;\n        &:hover {\n          background-color: @table-sticky-scroll-bar-active-bg;\n        }\n        &-active {\n          background-color: @table-sticky-scroll-bar-active-bg;\n        }\n      }\n    }\n  }\n}\n\n@media all and (-ms-high-contrast: none) {\n  .@{table-prefix-cls} {\n    &-ping-left {\n      .@{table-prefix-cls}-cell-fix-left-last::after {\n        box-shadow: none !important;\n      }\n    }\n    &-ping-right {\n      .@{table-prefix-cls}-cell-fix-right-first::after {\n        box-shadow: none !important;\n      }\n    }\n  }\n}\n\n@import './radius';\n@import './rtl';\n","/* stylelint-disable */\n.bezierEasingMixin() {\n@functions: ~`(function() {\n  var NEWTON_ITERATIONS = 4;\n  var NEWTON_MIN_SLOPE = 0.001;\n  var SUBDIVISION_PRECISION = 0.0000001;\n  var SUBDIVISION_MAX_ITERATIONS = 10;\n\n  var kSplineTableSize = 11;\n  var kSampleStepSize = 1.0 / (kSplineTableSize - 1.0);\n\n  var float32ArraySupported = typeof Float32Array === 'function';\n\n  function A (aA1, aA2) { return 1.0 - 3.0 * aA2 + 3.0 * aA1; }\n  function B (aA1, aA2) { return 3.0 * aA2 - 6.0 * aA1; }\n  function C (aA1)      { return 3.0 * aA1; }\n\n  // Returns x(t) given t, x1, and x2, or y(t) given t, y1, and y2.\n  function calcBezier (aT, aA1, aA2) { return ((A(aA1, aA2) * aT + B(aA1, aA2)) * aT + C(aA1)) * aT; }\n\n  // Returns dx/dt given t, x1, and x2, or dy/dt given t, y1, and y2.\n  function getSlope (aT, aA1, aA2) { return 3.0 * A(aA1, aA2) * aT * aT + 2.0 * B(aA1, aA2) * aT + C(aA1); }\n\n  function binarySubdivide (aX, aA, aB, mX1, mX2) {\n    var currentX, currentT, i = 0;\n    do {\n      currentT = aA + (aB - aA) / 2.0;\n      currentX = calcBezier(currentT, mX1, mX2) - aX;\n      if (currentX > 0.0) {\n        aB = currentT;\n      } else {\n        aA = currentT;\n      }\n    } while (Math.abs(currentX) > SUBDIVISION_PRECISION && ++i < SUBDIVISION_MAX_ITERATIONS);\n    return currentT;\n  }\n\n  function newtonRaphsonIterate (aX, aGuessT, mX1, mX2) {\n   for (var i = 0; i < NEWTON_ITERATIONS; ++i) {\n     var currentSlope = getSlope(aGuessT, mX1, mX2);\n     if (currentSlope === 0.0) {\n       return aGuessT;\n     }\n     var currentX = calcBezier(aGuessT, mX1, mX2) - aX;\n     aGuessT -= currentX / currentSlope;\n   }\n   return aGuessT;\n  }\n\n  var BezierEasing = function (mX1, mY1, mX2, mY2) {\n    if (!(0 <= mX1 && mX1 <= 1 && 0 <= mX2 && mX2 <= 1)) {\n      throw new Error('bezier x values must be in [0, 1] range');\n    }\n\n    // Precompute samples table\n    var sampleValues = float32ArraySupported ? new Float32Array(kSplineTableSize) : new Array(kSplineTableSize);\n    if (mX1 !== mY1 || mX2 !== mY2) {\n      for (var i = 0; i < kSplineTableSize; ++i) {\n        sampleValues[i] = calcBezier(i * kSampleStepSize, mX1, mX2);\n      }\n    }\n\n    function getTForX (aX) {\n      var intervalStart = 0.0;\n      var currentSample = 1;\n      var lastSample = kSplineTableSize - 1;\n\n      for (; currentSample !== lastSample && sampleValues[currentSample] <= aX; ++currentSample) {\n        intervalStart += kSampleStepSize;\n      }\n      --currentSample;\n\n      // Interpolate to provide an initial guess for t\n      var dist = (aX - sampleValues[currentSample]) / (sampleValues[currentSample + 1] - sampleValues[currentSample]);\n      var guessForT = intervalStart + dist * kSampleStepSize;\n\n      var initialSlope = getSlope(guessForT, mX1, mX2);\n      if (initialSlope >= NEWTON_MIN_SLOPE) {\n        return newtonRaphsonIterate(aX, guessForT, mX1, mX2);\n      } else if (initialSlope === 0.0) {\n        return guessForT;\n      } else {\n        return binarySubdivide(aX, intervalStart, intervalStart + kSampleStepSize, mX1, mX2);\n      }\n    }\n\n    return function BezierEasing (x) {\n      if (mX1 === mY1 && mX2 === mY2) {\n        return x; // linear\n      }\n      // Because JavaScript number are imprecise, we should guarantee the extremes are right.\n      if (x === 0) {\n        return 0;\n      }\n      if (x === 1) {\n        return 1;\n      }\n      return calcBezier(getTForX(x), mY1, mY2);\n    };\n  };\n\n  this.colorEasing = BezierEasing(0.26, 0.09, 0.37, 0.18);\n  // less 3 requires a return\n  return '';\n})()`;\n}\n// It is hacky way to make this function will be compiled preferentially by less\n// resolve error: `ReferenceError: colorPalette is not defined`\n// https://github.com/ant-design/ant-motion/issues/44\n.bezierEasingMixin();\n","@import '.